<template>
	<view class="box">
		<view class="text1" v-for="item in list">{{item}}</view>
	</view>
</template>

<script>
	export default{
		data(){
			return{
				list:['java','前端','pathone','PHP']
			}
		},
		onPullDownRefresh(){
			console.log('触发刷新')
			setTimeout(()=>{
				this.list.push(...['1','2','3','4'])
				uni.stopPullDownRefresh()
			},500)
		},
		onReachBottom(){
			this.list.push(...['1','2','3','4'])
		}
	}
</script>

<style scoped lang="scss">
	.box{
		background-color: $uni-bg-color;
		height: 200rpx;
		width: 200rpx;
		.text1{
			color: $uni-text-color-grey;
			height: 100px;
			line-height: 100px;
		}
	}
</style>
